@import '~bootstrap/dist/css/bootstrap.css';

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: scroll;
  /* background: url(https://www.insperity.com/wp-content/uploads/Remote_work_1200x630.png); */
  /* background: url(https://www.rd.com/wp-content/uploads/2020/07/GettyImages-1158679440.jpg); */
